* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

ul {
  list-style: none;
  padding-left: 0;
}

li {
  display: flex;
  margin-bottom: 10px;
}

h1,
p,
b{
  margin-top: 0;
  margin-bottom: 0;
}

.list-headline{
  margin-left: 10px;
}

p{
  margin-left: 5px;
}

.container-little{
  display: flex;
}
